import React from 'react';
import { motion } from 'framer-motion';
import Button from '../components/Button';
import { useTheme } from '../contexts/ThemeContext';
import { colors, typography } from '../styles/sharedStyles';

function Services() {
  const { darkMode } = useTheme();

  const services = [
    {
      title: "Derecho Corporativo y Comercial",
      items: [
        "Estructuración de empresas y vehículos de inversión",
        "Due diligence legal y financiero",
        "Negociación y redacción de contratos comerciales",
        "Implementación de programas de gobierno corporativo",
        "Asesoría en expansión internacional",
      ],
      benefit: "Minimización de riesgos legales y optimización de estructuras corporativas."
    },
    {
      title: "Propiedad Intelectual y Tecnología",
      items: [
        "Estrategias de protección de PI para startups",
        "Litigios de infracción de patentes",
        "Negociación de licencias de software",
        "Auditorías de cumplimiento en protección de datos",
        "Asesoría legal en blockchain y smart contracts",
      ],
      benefit: "Protección efectiva de activos intangibles y posicionamiento competitivo."
    },
    {
      title: "Derecho Laboral y Recursos Humanos",
      items: [
        "Diseño de políticas de teletrabajo",
        "Asesoría en procesos de reestructuración",
        "Mediación en conflictos laborales",
        "Implementación de programas de equidad",
        "Auditorías de seguridad y salud ocupacional",
      ],
      benefit: "Creación de ambientes laborales productivos y legalmente seguros."
    },
    {
      title: "Derecho Ambiental y Sostenibilidad",
      items: [
        "Obtención de permisos ambientales",
        "Defensa legal en casos de daño ambiental",
        "Implementación de sistemas de gestión ambiental",
        "Estrategias de economía circular",
        "Negociación de créditos de carbono",
      ],
      benefit: "Alineación de operaciones con estándares de sostenibilidad."
    },
  ];

  return (
    <div className={`${colors.background} ${typography.sans} ${darkMode ? 'dark' : ''} min-h-screen`}>
      <section className={`${colors.primary} text-white py-16`}>
        <div className="container mx-auto px-4">
          <motion.h1 
            className="text-4xl font-bold mb-4 text-center"
            initial={{ opacity: 0, y: -20 }}
            animate={{ opacity: 1, y: 0 }}
            transition={{ duration: 0.5 }}
          >
            Servicios Legales Especializados
          </motion.h1>
          <motion.p 
            className="text-xl text-center"
            initial={{ opacity: 0, y: -20 }}
            animate={{ opacity: 1, y: 0 }}
            transition={{ duration: 0.5, delay: 0.2 }}
          >
            Soluciones jurídicas innovadoras para los desafíos empresariales modernos
          </motion.p>
        </div>
      </section>

      <section className="py-16">
        <div className="container mx-auto px-4">
          <div className="grid grid-cols-1 md:grid-cols-2 gap-8">
            {services.map((service, index) => (
              <motion.div 
                key={index}
                className="bg-white dark:bg-gray-800 rounded-lg shadow-lg p-6"
                initial={{ opacity: 0, y: 20 }}
                animate={{ opacity: 1, y: 0 }}
                transition={{ duration: 0.5, delay: index * 0.1 }}
              >
                <h2 className="text-2xl font-semibold mb-4 dark:text-white">{service.title}</h2>
                <ul className="list-disc list-inside space-y-2 mb-4">
                  {service.items.map((item, itemIndex) => (
                    <li key={itemIndex} className={`${colors.text} dark:text-gray-300`}>{item}</li>
                  ))}
                </ul>
                <p className={`${colors.accent} font-semibold dark:text-blue-400`}>{service.benefit}</p>
              </motion.div>
            ))}
          </div>
        </div>
      </section>

      <section className={`${colors.secondary} text-white py-16`}>
        <div className="container mx-auto px-4 text-center">
          <h2 className="text-3xl font-bold mb-8">Compromiso con la Excelencia</h2>
          <p className="text-xl mb-8">En Bufete Salas Granados, nos dedicamos a proporcionar servicios legales de la más alta calidad, adaptados a las necesidades específicas de cada cliente.</p>
          <Button to="/contacto" variant="secondary">Solicitar Consulta Personalizada</Button>
        </div>
      </section>
    </div>
  );
}

export default Services;