import React from 'react';
import { Link } from 'react-router-dom';
import { motion } from 'framer-motion';

const variants = {
  primary: 'bg-blue-600 hover:bg-blue-700 text-white',
  secondary: 'bg-gray-200 hover:bg-gray-300 text-gray-800',
  outline: 'bg-transparent hover:bg-blue-50 text-blue-600 border border-blue-600',
};

const Button = ({ children, to, variant = 'primary', className = '', ...props }) => {
  const baseStyle = 'px-4 py-2 rounded font-semibold transition duration-300 ease-in-out focus:outline-none focus:ring-2 focus:ring-blue-500 focus:ring-opacity-50';
  const style = `${baseStyle} ${variants[variant]} ${className}`;

  const MotionComponent = motion(to ? Link : 'button');

  return (
    <MotionComponent
      to={to}
      className={style}
      whileHover={{ scale: 1.05 }}
      whileTap={{ scale: 0.95 }}
      {...props}
    >
      {children}
    </MotionComponent>
  );
};

export default Button;